html {
    background-color: rgb(249 250 251); /* bg-gray-50 */
    transition: background-color 0.3s ease, color 0.3s ease;
    /* Add scroll padding to account for sticky header + top bar */
    scroll-padding-top: 108px; /* Mobile - precise header height */
    /* Enable smooth scrolling for anchor links */
    scroll-behavior: smooth;
    /* Better font rendering */
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

@media (min-width: 768px) {
    html {
      scroll-padding-top: 112px; /* Desktop - slightly larger header */
    }
  }

/* Dark mode styles */

html.dark {
    background-color: rgb(17 24 39); /* bg-gray-900 */
    color: rgb(243 244 246); /* text-gray-100 */
  }

body {
    font-family: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', sans-serif;
    color: rgb(17 24 39); /* text-gray-900 */
    background-color: rgb(249 250 251); /* bg-gray-50 */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    transition: background-color 0.3s ease, color 0.3s ease;
    margin: 0;
    padding: 0;
    line-height: 1.6;
    font-size: 16px;
  }

/* Mobile-first typography */

@media (max-width: 640px) {
    body {
      font-size: 14px;
      line-height: 1.5;
    }
  }

.dark body {
    color: rgb(243 244 246); /* text-gray-100 */
    background-color: rgb(17 24 39); /* bg-gray-900 */
  }

/* Enhanced typography classes */

.font-display {
    font-family: 'Playfair Display', 'Georgia', 'Times New Roman', serif;
  }

.font-body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  }

.font-heading {
    font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  }

/* Responsive text sizing utilities */

.text-responsive-xs {
    font-size: 0.75rem; /* 12px */
    line-height: 1.4;
  }

.text-responsive-sm {
    font-size: 0.875rem; /* 14px */
    line-height: 1.5;
  }

.text-responsive-base {
    font-size: 0.875rem; /* 14px mobile */
    line-height: 1.5;
  }

.text-responsive-lg {
    font-size: 1rem; /* 16px mobile */
    line-height: 1.6;
  }

.text-responsive-xl {
    font-size: 1.125rem; /* 18px mobile */
    line-height: 1.6;
  }

.text-responsive-2xl {
    font-size: 1.25rem; /* 20px mobile */
    line-height: 1.5;
  }

.text-responsive-3xl {
    font-size: 1.5rem; /* 24px mobile */
    line-height: 1.4;
  }

.text-responsive-4xl {
    font-size: 1.875rem; /* 30px mobile */
    line-height: 1.3;
  }

.text-responsive-5xl {
    font-size: 2.25rem; /* 36px mobile */
    line-height: 1.2;
  }

/* Desktop scaling */

@media (min-width: 768px) {
    .text-responsive-base {
      font-size: 1rem; /* 16px */
    }
    
    .text-responsive-lg {
      font-size: 1.125rem; /* 18px */
    }
    
    .text-responsive-xl {
      font-size: 1.25rem; /* 20px */
    }
    
    .text-responsive-2xl {
      font-size: 1.5rem; /* 24px */
    }
    
    .text-responsive-3xl {
      font-size: 1.875rem; /* 30px */
    }
    
    .text-responsive-4xl {
      font-size: 2.25rem; /* 36px */
    }
    
    .text-responsive-5xl {
      font-size: 3rem; /* 48px */
    }
  }

@media (min-width: 1024px) {
    .text-responsive-2xl {
      font-size: 1.75rem; /* 28px */
    }
    
    .text-responsive-3xl {
      font-size: 2.25rem; /* 36px */
    }
    
    .text-responsive-4xl {
      font-size: 3rem; /* 48px */
    }
    
    .text-responsive-5xl {
      font-size: 3.75rem; /* 60px */
    }
  }

/* Section scroll positioning - minimal gap */

section[id] {
    scroll-margin-top: 108px; /* Mobile - precise header height */
  }

@media (min-width: 768px) {
    section[id] {
      scroll-margin-top: 112px; /* Desktop - precise header height */
    }
  }

.dark body {
    color: rgb(243 244 246); /* text-gray-100 */
    background-color: rgb(17 24 39); /* bg-gray-900 */
  }

h1, h2, h3, h4, h5, h6 {
    font-weight: 600; /* font-semibold */
    line-height: 1.25; /* leading-tight */
  }

h1 {
    font-size: 2.25rem; /* text-4xl */
  }

@media (min-width: 1024px) {
    h1 {
      font-size: 3.75rem; /* lg:text-6xl */
    }
  }

h2 {
    font-size: 1.875rem; /* text-3xl */
  }

@media (min-width: 1024px) {
    h2 {
      font-size: 2.25rem; /* lg:text-4xl */
    }
  }

h3 {
    font-size: 1.5rem; /* text-2xl */
  }

@media (min-width: 1024px) {
    h3 {
